.similar {
	padding-top: 50px;
	&__title {
		margin-bottom: 50px;
		letter-spacing: .01em;
		@include clr; }
	&__link {
		float: right;
		margin: 10px 0 0 20px;
		color: $blue;
		@include f(14px, null, 1);
		@include hover {
			color: $lightBlue; } }
	&__products {
		font-size: 0;
		margin: 0 -30px; }
	&__wrapper {
		display: inline-block;
		vertical-align: top;
		width: 25%;
		padding: 0 30px; }
	&__item {
		display: block;
		text-align: center;
		color: $dark; }
	&__img {
		height: 155px;
		margin-bottom: 16px;
		@include vertical;
		&-in {
			display: inline-block;
			vertical-align: middle;
			max-width: 100%;
			max-height: 100%; } }
	&__label {
		margin-bottom: 5px;
		@include f(14px, null, 1.5);
		letter-spacing: .01em; }
	&__price {
		@include f(14px, 700, 2.143);
		letter-spacing: .01em; } }
