.card {
	margin: 50px 0;
	&__row {
		margin: 0 -15px;
		@include clr; }
	&__col {
		padding: 0 15px;
		float: left;
		&_41 {
			width: 41.66666667%; }
		&_58 {
			width: 58.33333333%; } }
	&__case {
		padding: 20px 0; }
	&__wrapper {
		padding-left: 50px; }
	&__info {
		padding: 25px 30px 25px 50px;
		box-shadow: 0 0 18px 0 rgba($black, .1); }
	&__title {
		font-family: $montserrat;
		@include f(21px, 500, 1.524);
		letter-spacing: .025em;
		margin-bottom: 10px; }
	&__meta {
		margin: -10px;
		padding-bottom: 40px;
		font-size: 0;
		li {
			display: inline-block;
			margin: 10px;
			color: $gray-lighten;
			@include f(14px, null, 1);
			letter-spacing: .01em;
			span {
				color: #202020; } } }
	&__characteristics {
		padding-bottom: 40px; }
	&__total {
		font-size: 0; }
	&__price,
	&__qua,
	&__availability {
		display: inline-block;
		vertical-align: top;
		margin-bottom: 25px; }
	&__price {
		letter-spacing: .025em;
		color: #202020;
		margin-right: 20px;
		min-width: 175px;
		@include f(30px, 500, 1.2);
		span {
			@include f(null, 700, null); } }
	&__qua {
		width: 80px;
		margin-right: 20px; }
	&__counter {
		padding: 5px;
		.counter__input {
			margin-left: 0;
			text-align: center; } }
	&__availability {
		padding: 11px 0;
		@include f(14px, null, 1);
		span {
			color: rgba($dark, .5); } }
	&__progress {
		font-size: 0;
		display: inline-block;
		vertical-align: top;
		margin-left: 7px;
		padding: 2px 0;
		&-item {
			display: inline-block;
			vertical-align: top;
			width: 2px;
			height: 10px;
			margin: 0 1px;
			background-color: #cfcfcf;
			&.is-active {
				background-color: $orange; } } }
	&__controls {
		font-size: 0;
		margin: 0 -8px -15px;
		&-btn {
			min-width: 130px;
			padding: 10px 10px 11px; }
		&-icon {
			font-size: 19px;
			vertical-align: middle;
			margin-right: 10px; } }
	&__control {
		margin: 0 8px 15px;
		&_indent {
			margin-right: 20px; } }
	&__action {
		background: transparent;
		font-size: 0;
		padding: 8px 0;
		@include hover {
			span {
				color: $lightBlue; } }
		span {
			display: inline-block;
			vertical-align: middle;
			transition: color .24s;
			color: $blue;
			@include f(14px, null, 1); } } }
.case {
	&__logo {
		position: absolute;
		right: 0;
		top: 0;
		max-width: 94px;
		max-height: 31px; }
	&__slider {
		position: relative; }
	&__nav {
		position: absolute;
		left: 0;
		top: 0;
		width: 80px;
		height: 100%;
		.slick-current {
			.case__item_small {
				&:after {
					background: $blue; } }
			img {
				opacity: 1; } } }
	&__for {
		padding-left: 120px; }
	&__item {
		@include vertical;
		img {
			display: inline-block;
			vertical-align: middle;
			max-width: 100%;
			max-height: 100%; }
		&_big {
			margin-right: auto;
			margin-left: auto;
			height: 460px; }
		&_small {
			height: 100px;
			position: relative;
			cursor: pointer;
			&:after {
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				height: 3px;
				background: transparent;
				transition: background .24s; }
			img {
				opacity: .6;
				transition: opacity .24s; } } } }
