  font-size: 13px;
  font-weight: 400;
  line-height: 1.847;
  letter-spacing: 0.025em;
  transition: color 0.3s;
}

.no-touch .breadcrumbs__link:hover {
  color: #0caafe;
}

.heading {
  padding: 20px 0;
  border-bottom: 1px solid #dcdcdc;
}

.heading__title {
  margin-top: 13px;
}

.range {
  position: relative;
  display: flex;
  border: 1px solid #dcdcdc;
  border-radius: 5px;
}

.range:before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  bottom: 0;
  border-left: 1px solid #dcdcdc;
}

.range__field {
  display: flex;
  align-items: center;
  width: 50%;
}

.range__label {
  display: block;
  padding-left: 8px;
  min-width: 65px;
  opacity: 0.5;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.range__input {
  flex-grow: 1;
  padding-right: 8px;
  width: auto;
  min-width: 1px;
  height: 33px;
  background: none;
  font-family: "Open Sans", Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
}

.sidebar__checkbox {
  padding: 2px 0;
}

.sidebar__checkboxes {
  padding-bottom: 26px;
}

.sidebar__checkboxes_in {
  display: none;
  padding: 0;
}

.sidebar__list {
  padding-top: 20px;
}

.sidebar__question {
  position: absolute;
  top: 9px;
  right: 0;
  width: 1em;
  height: 1em;
  background: #e5e5e5;
  font-size: 17px;
}

.sidebar__question .icon {
  display: block;
  font-size: 1em;
}

.sidebar__link {
  position: relative;
  display: block;
  padding-right: 12px;
  color: inherit;
  transition: color 0.3s;
}

.no-touch .sidebar__link:hover {
  color: #ffae1f;
}

.no-touch .sidebar__link:hover .sidebar__link-icon {
  fill: #ffae1f;
}

.sidebar__link.is-active {
  color: #ffae1f;
}

.sidebar__link.is-active .sidebar__link-icon {
  fill: #ffae1f;
}

.sidebar__link-title {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 6px 20px 6px 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.sidebar__link-icon {
  position: absolute;
  top: 15px;
  right: 0;
  fill: #797979;
  font-size: 5px;
  transition: fill 0.3s;
}

.catalog {
  border-bottom: 1px solid #dcdcdc;
}

.catalog__inner {
  display: flex;
}

.catalog__sidebar {
  padding: 30px 20px 30px 0;
  border-right: 1px solid #dcdcdc;
  width: 280px;
}

.checkbox_updated {
  padding-left: 30px;
}

.map {
  width: 100%;
  height: 600px;
}

.similar {
  padding-top: 50px;
}

.similar__title {
  margin-bottom: 50px;
  letter-spacing: .01em;
}

.similar__title:after {
  content: " ";
  display: table;
  clear: both;
}

.similar__link {
  float: right;
  margin: 10px 0 0 20px;
  color: #0caafe;
  font-size: 14px;
  line-height: 1;
}

.no-touch .similar__link:hover {
  color: #37baff;
}

.similar__products {
  font-size: 0;
  margin: 0 -30px;
}

.similar__wrapper {
  display: inline-block;
  vertical-align: top;
  width: 25%;
  padding: 0 30px;
}

.similar__item {
  display: block;
  text-align: center;
  color: #0f2841;
}

.similar__img {
  height: 155px;
  margin-bottom: 16px;
  text-align: center;
  font-size: 0;
}

.similar__img:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.similar__img-in {
  display: inline-block;
  vertical-align: middle;
  max-width: 100%;
  max-height: 100%;
}

.similar__label {
  margin-bottom: 5px;
  font-size: 14px;
  line-height: 1.5;
  letter-spacing: .01em;
}

.similar__price {
  font-size: 14px;
  font-weight: 700;
  line-height: 2.143;
  letter-spacing: .01em;
}
